
.tiles-select {
  position: absolute;
  bottom: 10px;
  right: 10px;
  z-index: 5000;
}
.tiles-select button {
    border: none;
    height: 40px;
    width: 110px;
}
.tiles-select button.none {
      background-color: #70C5FB;
}
.tiles-select button.tree {
      background-color: #2EA31E;
}
.tiles-select button.wild {
      background-color: #FABF71;
}
.tiles-select button.blue {
      background-color: #7263DA;
}
.tiles-select button:hover {
      cursor: pointer;
      color: #fff;
}



